.item {
	-webkit-transition: background-color 1.5s linear, color 1.5s linear;
	-o-transition: background-color 1.5s linear, color 1.5s linear;
	transition: background-color 1.5s linear, color 1.5s linear;
}

.menu {
	-ms-flex-flow: wrap;
	    flex-flow: wrap;
}

.menu > .item:last-child {
	margin-right: 2rem;
}

@media only screen and (max-width: 897px) {
	.menu:not(.ui) {
		width: 100%;
		-ms-flex-pack: center;
		    justify-content: center;
	}

	.menu > .item:last-child {
		margin-right: 0;
	}

	.ui.menu > .item:first-child {
		border-radius: 0;
	}
}





